import client from '../helpers/HttpClient'
const endpoint = '/spaces';
const formDataHeader = {
    headers: {
        'Content-Type': 'multipart/form-data'
    }
}
export default {
    async update(payload) {
        const { id, data } = payload;
        const path = `${endpoint}/${id}`;
        let response = await client.put(path, data);
        return response.data;
    },
    async delete(id) {
        const path = `${endpoint}/${id}`;
        let response = await client.delete(path);
        return response.data;
    },
    async addImage(payload) {
        const { data, id } = payload;
        const path = `${endpoint}/${id}/images`;
        let response = await client.post(path, data, formDataHeader)
        return response.data;
    },
    async get(id) {
        const path = `${endpoint}/${id}`;
        let response = await client.get(path);
        return response.data;
    }
}
