import React from "react";
import { connect } from "react-redux";
// nodejs library that concatenates classes
import { Link } from "react-router-dom";
import classnames from "classnames";

// reactstrap components
import ReactDatetime from "react-datetime";
import _ from "lodash";
import { toast } from "react-toastify";
import {
  Button,
  Card,
  Label,
  CardBody,
  FormGroup,
  Form,
  Container,
  Row,
  Col,
  Table,
  ListGroup,
  ListGroupItem,
} from "reactstrap";
// core components

import {
  getTime,
  getDate,
  getMomentObject,
  isGTENowDay,
} from "helpers/UtilsHelper";
import { FormInput, ImageSlider } from "components/Form";

import venueApi from "api/venueApi";
import spaceApi from "api/spaceApi";
import spaceImageApi from "api/spaceImageApi";
import { removeVenue } from "../../../redux/actions/venuesActions";
import ConfirmDeleteModal from "./ConfirmDeleteModal.jsx";
import systemSettingsApi from "../../../api/systemSettingsApi";

import "./Venues.scss";

const startTimeDefault = "8:00";
const closeTimeDefault = "20:00";
const dayOnWeek = [
  "monday",
  "tuesday",
  "wednesday",
  "thursday",
  "friday",
  "saturday",
  "sunday",
];
const operatingHoursData = dayOnWeek.map((item, index) => {
  return {
    dayOfWeek: index + 1,
    name: item,
    startTime: startTimeDefault,
    closeTime: closeTimeDefault,
    isUsable: false,
  };
});

const tierData = ["tier 1", "tier 2", "tier 3", "tier 3"].map((item, index) => {
  return {
    tier: index + 1,
    hour: null,
  };
});

class Spaces extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      spaceId: null,
      name: null,
      images: [],
      floor: null,
      operatingHours: operatingHoursData,
      blackoutDates: [],
      errors: {},
      isFormReady: {},
      spaceList: [],
      prices: [],
      tier: tierData,
    };
  }

  deleteSpace = () => {
    const { spaceId } = this.state;
    spaceApi
      .delete(spaceId)
      .then(() => {
        toast.success("Delete space successfully");
        this.props.history.push("/admin/venues");
      })
      .catch(() => {
        toast.error("Delete space failed");
      });
  };

  toggleConfirmDelete = () => {
    this.setState({ isDeleteVenue: !this.state.isDeleteVenue });
  };

  addBlackoutDates = (data) => {
    this.setState({
      blackoutDates: [
        { date: null, disabled: false },
        ...this.state.blackoutDates,
      ],
    });
  };

  setBlackoutDates = (e, index) => {
    const date = new Date(e._d + "");
    let tempData = this.state.blackoutDates;
    tempData[index] = { date: getDate(date) };
    this.setState({ blackoutDates: tempData });
  };

  deleteBlackoutDates = (index) => {
    let tempArr = this.state.blackoutDates;
    tempArr.splice(index, 1);
    this.setState({ blackoutDates: tempArr });
  };

  blackoutDateValidate = (currentDate) => {
    const { blackoutDates } = this.state;
    let check = false;
    blackoutDates.forEach((item) => {
      check = check || getDate(currentDate) === item.date;
    });
    return isGTENowDay(currentDate) && !check;
  };

  handleError = (field, status) => {
    let check = true;
    if (status) {
      for (let key in this.state.isFormReady) {
        check = check && this.state.isFormReady[key];
      }
    }
    this.setState({
      isFormReady: {
        ...this.state.isFormReady,
        [field]: status,
        check: check && status,
      },
    });
  };

  handleChange = (e) => {
    const { name, value } = e.target;
    delete this.state.errors[name];
    this.setState({ [name]: value });
  };

  checkHourTierValidate = () => {
    const { prices } = this.state;
    prices.forEach((item, index) => {
      const name = `tier_${index}_hour`;
      delete this.state.errors[name];
      const currentData = prices[index] && Number(prices[index]["hour"]);
      const preData = prices[index - 1] && Number(prices[index - 1]["hour"]);
      const nextData = prices[index + 1] && Number(prices[index + 1]["hour"]);
      if (index === 0) {
        if (currentData && nextData && currentData >= nextData) {
          this.setState({
            errors: {
              ...this.state.errors,
              [name]: ["Data should be less than next Tier"],
            },
          });
        }
      } else if (index < 3) {
        if (preData && currentData && currentData <= preData) {
          this.setState({
            errors: {
              ...this.state.errors,
              [name]: ["Data should be lager than previous Tier"],
            },
          });
        } else if (currentData && nextData && currentData >= nextData) {
          this.setState({
            errors: {
              ...this.state.errors,
              [name]: ["Data should be less than next Tier"],
            },
          });
        }
      } else {
        if (preData && currentData && currentData <= preData) {
          this.setState({
            errors: {
              ...this.state.errors,
              [name]: ["Data should be lager than previous Tier"],
            },
          });
        }
      }
    });
  };

  handleChangePricing = (e) => {
    const name = e.target.name;
    const value = Number(e.target.value);
    const index = name.split("_")[1];
    const type = name.split("_")[2];
    const { prices } = this.state;
    prices[index][type] = value;
    this.setState({ prices }, () => {
      if (type === "hour") {
        this.checkHourTierValidate();
      }
    });
  };

  handleChangeTime = (e, index, type) => {
    const date = new Date(e._d + "");
    let tempData = this.state.operatingHours;
    if (
      type === "closeTime" &&
      date.getHours() === 0 &&
      date.getMinutes() === 0
    ) {
      date.setHours(23);
      date.setMinutes(59);
    }

    tempData[index][type] = getTime(date, "HH:mm");
    this.setState({ operatingHours: tempData });
  };

  handleCheckTime = (e) => {
    let tempData = this.state.operatingHours;
    const { name, checked } = e.target;
    tempData[Number(name)].isUsable = checked;
    this.setState({ operatingHours: tempData });
  };

  convertListFileToMap = (listFile) => {
    let arr = [];
    for (let i = 0; i < listFile.length; i++) {
      arr.push({ src: listFile[i] });
    }
    return arr;
  };

  handleFile = (event) => {
    const { name, files } = event.target;
    const { spaceId } = this.props.match.params;
    const listFile = this.convertListFileToMap(files);
    if (listFile.length) {
      let formData = new FormData();
      formData.append("file", files[0]);
      spaceApi
        .addImage({ id: spaceId, data: formData })
        .then((res) => {
          toast.success("Add image successfully");
          const { spaceList } = this.state;
          const currentSpace = spaceList.filter(
            (item) => item.id === spaceId
          )[0];
          currentSpace[name].push(res);
          this.setState(
            {
              [name]: [res, ...this.state[name]],
              spaceList,
            },
            () => {
              this.imageList.value = null;
            }
          );
        })
        .catch((err) => {
          toast.error("Upload image failed");
        });
    }
  };

  handleDeleteFile = (id, index) => {
    spaceImageApi
      .delete(id)
      .then((res) => {
        toast.success("Delete image successfully");
        const tempArr = this.state.images;
        tempArr.splice(index, 1);
        this.setState({ images: tempArr });
      })
      .catch(() => {
        toast.error("Delete image failed");
      });
  };

  handleError = (field, status) => {
    let check = true;
    if (status) {
      for (let key in this.state.isFormReady) {
        check = check && this.state.isFormReady[key];
      }
    }
    this.setState({
      isFormReady: {
        ...this.state.isFormReady,
        [field]: status,
        check: check && status,
      },
    });
  };

  selectSpace = (index) => {
    const { venueId } = this.props.match.params;
    const currentSpace = this.state.spaceList[index];
    this.setState({ ...this.getSpaceObject(currentSpace) }, () => {
      this.props.history.push(
        `/admin/venues/${venueId}/spaces/${currentSpace.id}`
      );
    });
  };

  saveForm = (e) => {
    e.preventDefault();
    const { venueId } = this.props.match.params;
    const { spaceId, name, floor, operatingHours, blackoutDates, prices } =
      this.state;
    const submitData = {
      name,
      floor,
      operatingHours: JSON.stringify(operatingHours),
      blackoutDates: JSON.stringify(
        blackoutDates.filter((item) => item.date && !item.disabled)
      ),
      prices: JSON.stringify(prices),
      venue: venueId,
    };
    spaceApi
      .update({ id: spaceId, data: submitData })
      .then((res) => {
        const spaceList = this.state.spaceList.map((item) => {
          return item.id === spaceId ? res : item;
        });
        this.setState({ spaceList });
        toast.success("Update spaces successfully");
      })
      .catch((err) => {
        const errors = err.response.data;
        this.setState({ errors });
        toast.error("Update spaces failed");
      });
  };

  getBlackoutDateFromVenue = () => {
    const { venueId } = this.props.match.params;
    venueApi.detail(venueId).then((res) => {
      const { blackoutDates } = res;
      const parentBlackoutDates = blackoutDates.map((item) => ({
        date: item.date,
        disabled: true,
      }));
      this.setState({
        blackoutDates: [...parentBlackoutDates, ...this.state.blackoutDates],
      });
    });
  };

  getTierSetting = () => {
    systemSettingsApi.getTier().then((res) => {
      const valueArr = res && res.value.split(",");
      const tier = this.state.tier.map((item, index) => {
        item.hour = Number(valueArr[index]);
        return item;
      });
      this.setState({ tier });
    });
  };

  getSpaceObject = (currentSpace) => {
    try {
      const { name, blackoutDates, floor, operatingHours, images, id, prices } =
        currentSpace;
      const operatingHoursUpdated = operatingHours.map((item, index) => {
        item.name = dayOnWeek[index];
        return item;
      });
      return {
        name,
        blackoutDates,
        floor,
        operatingHours: operatingHoursUpdated,
        images,
        spaceId: id,
        prices,
      };
    } catch (error) {
      return null;
    }
  };

  componentDidMount() {
    const { venueId, spaceId } = this.props.match.params;
    if (venueId && spaceId) {
      venueApi.spaces(venueId).then((res) => {
        if (res.result && res.result.length > 0) {
          const spaceList = res.result;
          const currentSpace = spaceList.filter(
            (item) => item.id === spaceId
          )[0];
          this.setState(
            {
              ...this.getSpaceObject(currentSpace),
              spaceList,
              currentSpace,
            },
            () => {
              this.getBlackoutDateFromVenue();
              this.getTierSetting();
            }
          );
        }
      });
    }
  }

  render() {
    const {
      name,
      blackoutDates,
      images,
      operatingHours,
      floor,
      errors,
      isFormReady,
      spaceList,
      spaceId,
      prices,
      tier,
    } = this.state;
    const formValid =
      name &&
      images &&
      !_.isEmpty(blackoutDates) &&
      !_.isEmpty(operatingHours) &&
      isFormReady.check;
    return (
      <>
        <ConfirmDeleteModal
          className="modal-dialog-centered"
          isOpen={this.state.isDeleteVenue}
          toggle={this.toggleConfirmDelete}
          handle={this.deleteSpace}
          type="Space"
        />
        <Container className="venues mt--12" fluid>
          <Card className="mb-4">
            <CardBody>
              <Form onSubmit={this.saveForm}>
                <Row>
                  <Col md="10" lg="8" className="order-2 order-sm-1">
                    <FormGroup className="row">
                      <Label className="form-control-label" md="3">
                        Room Name
                      </Label>
                      <Col md="5">
                        <FormInput
                          required={true}
                          type="text"
                          name="name"
                          defaultValue={name}
                          handleError={this.handleError}
                          errors={errors["name"]}
                          onChange={this.handleChange}
                        />
                      </Col>
                    </FormGroup>
                    <FormGroup className="row">
                      <Label className="form-control-label" md="3">
                        Image
                      </Label>
                      <Col md="7">
                        <Card className="align-items-center venues__image-slider mb-0">
                          {images && images.length > 0 ? (
                            <ImageSlider
                              listImage={images}
                              handleDelete={this.handleDeleteFile}
                            />
                          ) : (
                            <div
                              className="venues__placeholder-image"
                              onClick={() => this.imageList.click()}
                            >
                              <div className="venues__placeholder-image__label">
                                No Image
                              </div>
                            </div>
                          )}
                          <input
                            ref={(ref) => (this.imageList = ref)}
                            onChange={this.handleFile}
                            style={{ display: "none" }}
                            type="file"
                            name="images"
                            id="roomImage"
                            accept=".jpg,.gif,.jpeg,.png"
                            multiple
                          />
                          <Button
                            className="mt-3 w-30"
                            color="default"
                            onClick={() => this.imageList.click()}
                          >
                            Add Image
                          </Button>
                          <div className="error-text mt-3">
                            {errors["images"]}
                          </div>
                        </Card>
                      </Col>
                    </FormGroup>
                    <FormGroup className="row">
                      <Label className="form-control-label" md="3">
                        Comments
                      </Label>
                      <Col md="5">
                        <FormInput
                          required={true}
                          type="text"
                          name="floor"
                          defaultValue={floor}
                          handleError={this.handleError}
                          errors={errors["floor"]}
                          onChange={this.handleChange}
                        />
                      </Col>
                    </FormGroup>
                    <FormGroup className="row">
                      <Label className="form-control-label" md="3">
                        Hours
                      </Label>
                      <Col md="5" className="venues__openrating-hours">
                        <Table className="venues__table-openrating-hours w-100">
                          <thead>
                            <tr>
                              <th width="50%"></th>
                              <th width="25%">Open</th>
                              <th width="25%">Close</th>
                            </tr>
                          </thead>
                          <tbody>
                            {operatingHours.map((item, index) => (
                              <tr key={index}>
                                <td>
                                  <div className="custom-control custom-checkbox">
                                    <input
                                      className="custom-control-input"
                                      id={item.name}
                                      name={index}
                                      type="checkbox"
                                      checked={item.isUsable}
                                      onChange={this.handleCheckTime}
                                    />
                                    <label
                                      className="custom-control-label text-capitalize"
                                      htmlFor={item.name}
                                    >
                                      {item.name}
                                    </label>
                                  </div>
                                </td>
                                <td>
                                  <ReactDatetime
                                    className="venues__select-time"
                                    value={getMomentObject(
                                      item.startTime,
                                      "HH:mm"
                                    )}
                                    timeFormat={true}
                                    dateFormat={false}
                                    closeOnSelect={true}
                                    renderInput={this.renderInputTime}
                                    onChange={(event) =>
                                      this.handleChangeTime(
                                        event,
                                        index,
                                        "startTime"
                                      )
                                    }
                                  />
                                </td>
                                <td>
                                  <ReactDatetime
                                    className="venues__select-time"
                                    value={getMomentObject(
                                      item.closeTime,
                                      "HH:mm"
                                    )}
                                    closeOnSelect={true}
                                    timeFormat={true}
                                    dateFormat={false}
                                    onChange={(event) =>
                                      this.handleChangeTime(
                                        event,
                                        index,
                                        "closeTime"
                                      )
                                    }
                                  />
                                </td>
                              </tr>
                            ))}
                          </tbody>
                        </Table>
                        <div className="error-text mt-3">
                          {errors["operatingHours"]}
                        </div>
                      </Col>
                    </FormGroup>
                    <FormGroup className="row">
                      <Label
                        className="form-control-label"
                        htmlFor="example-password-input"
                        md="3"
                      >
                        Blackout Dates
                      </Label>
                      <Col
                        md="7"
                        className={`venues__blackout-date ${
                          blackoutDates.length > 4
                            ? "venues__blackout-date--scroll"
                            : ""
                        }`}
                      >
                        <Row className="mb-3">
                          <Col lg="6">
                            <Button
                              className="w-5"
                              color="secondary"
                              onClick={this.addBlackoutDates}
                            >
                              &#x2b;
                            </Button>
                          </Col>
                        </Row>
                        {blackoutDates.map((item, index) => (
                          <Row key={index} className="mb-2">
                            <Col lg="6" sm="8" xs="8">
                              <ReactDatetime
                                inputProps={{
                                  placeholder: "Date Picker",
                                  disabled: item.disabled,
                                }}
                                closeOnSelect={true}
                                timeFormat={false}
                                dateFormat={true}
                                value={getMomentObject(item.date, "YYYY:MM:DD")}
                                onChange={(event) =>
                                  this.setBlackoutDates(event, index)
                                }
                                isValidDate={this.blackoutDateValidate}
                              />
                            </Col>
                            {!item.disabled && (
                              <Col md="2" xs="2">
                                <Button
                                  className="w-5"
                                  color="secondary"
                                  onClick={() =>
                                    this.deleteBlackoutDates(index)
                                  }
                                >
                                  &#8722;
                                </Button>
                              </Col>
                            )}
                          </Row>
                        ))}
                        <div className="error-text mt-3">
                          {errors["blackoutDates"]}
                        </div>
                      </Col>
                    </FormGroup>
                    <FormGroup className="row">
                      <Label className="form-control-label" md="3">
                        Pricing
                      </Label>
                      <Col md="8" className="venues__prices-section">
                        <Row>
                          <Col lg="6" sm="8" xs="8">
                            <Table>
                              <thead>
                                <tr>
                                  <th width="10">
                                    <strong>Tier</strong>
                                  </th>
                                  <th width="20">
                                    <strong>From (Hrs)</strong>
                                  </th>
                                  <th width="20">
                                    <strong>To (Hrs)</strong>
                                  </th>
                                  <th width="50">
                                    <strong>Cost ($)</strong>
                                  </th>
                                </tr>
                              </thead>
                              <tbody>
                                {prices.map((item, index) => (
                                  <tr key={index}>
                                    <td>
                                      <h5 className="venues__prices-section__item">{`Tier ${item.tier}`}</h5>
                                    </td>
                                    <td>
                                      <h5 className="venues__prices-section__item">
                                        {index > 0
                                          ? tier[index - 1].hour &&
                                            tier[index - 1].hour + 1
                                          : 1}
                                      </h5>
                                    </td>
                                    <td>
                                      <strong>{tier[index].hour}</strong>
                                    </td>
                                    <td>
                                      <FormInput
                                        regex="number"
                                        required={true}
                                        type="text"
                                        placeholder="$"
                                        name={`tier_${index}_cost`}
                                        value={item.cost}
                                        errors={errors[`tier_${index}_price`]}
                                        onChange={this.handleChangePricing}
                                        handleError={this.handleError}
                                      />
                                    </td>
                                  </tr>
                                ))}
                              </tbody>
                            </Table>
                          </Col>
                        </Row>
                      </Col>
                    </FormGroup>
                  </Col>
                  <Col
                    md="2"
                    lg="4"
                    className="venues__list-rom order-1 order-sm-2 "
                  >
                    <h3 className="form-control-label">Room list</h3>
                    <CardBody className="p-0">
                      <ListGroup data-toggle="checklist" flush>
                        {spaceList.map((item, index) => (
                          <ListGroupItem
                            className="py-2 px-2 cursor-pointer"
                            key={index}
                          >
                            <div
                              className={classnames(
                                "checklist-item checklist-item-success",
                                {
                                  "checklist-item--active": item.id === spaceId,
                                }
                              )}
                              onClick={() => this.selectSpace(index)}
                            >
                              <div className="checklist-info">
                                <h5 className="checklist-title my-2">
                                  {item.name}
                                </h5>
                              </div>
                            </div>
                          </ListGroupItem>
                        ))}
                      </ListGroup>
                    </CardBody>
                  </Col>
                </Row>
                <Row>
                  <Col md="6" className="my-3">
                    <Button color="danger" onClick={this.toggleConfirmDelete}>
                      Delete Space
                    </Button>
                  </Col>
                  <Col md="6" className="my-3">
                    <Link to={"/admin/venues"} className="mr-3">
                      <Button color="secondary">Cancel</Button>
                    </Link>
                    <Button
                      color="success"
                      onClick={this.saveForm}
                      disabled={false && formValid}
                    >
                      Save Changes
                    </Button>
                  </Col>
                </Row>
              </Form>
            </CardBody>
          </Card>
        </Container>
      </>
    );
  }
}

const mapActionToProps = { removeVenue };

export default connect(null, mapActionToProps)(Spaces);
