import client from '../helpers/HttpClient'
const endpoint = '/systemsettings'
export default {
    async getTier() {
        const path = `${endpoint}/provider_tiers`
        let response = await client.get(path)
        return response.data;
    },
    async setTier(data) {
        const path = `${endpoint}/provider_tiers`;
        let response = await client.patch(path, data);
        return response.data;
    }
}
